import PenIcon from "@/assets/icons/Pen.vue";
export default {
  name: "ContractReviewModal",
  components: {
    ModalCloseIcon,
    ChevronIcon,
    FlagIcon,
    PenIcon
  },
  data() {
    return {
      step: 1,
      answer1: "mail",
      answer2: "quarterly",
      answer3: 45,
      answer4: 45,
      answer5: 45,
      answer6: 45,
      answer7: 45
    };
  },
  methods: {
    changeStep(step) {
      this.step = step;
    },
    prevStep() {
      if (this.step > 1) {
        this.step--;
      }
    },
    nextStep() {
      if (this.step < 3) {
        this.step++;
      }
    }
  }
};
