<template>
  <div class="cards-list">
    <div
      class="card"
      v-for="(type, i) in ['Visa', 'Mastercard', 'Paypal']"
      :key="i"
      @click="selectCard(i)"
      :class="{
        selected: isSelected(i),
        notselected: isNotSelected(i)
      }"
    >
      <div class="brand">
        <img :src="require(`@/assets/icons/Credit-Card-${type}.svg`)" />
      </div>
      <div class="number">
        <div class="dots">
          <div v-for="i in 3" :key="i" class="dot-group">
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
            <div class="dot"></div>
          </div>
        </div>
        <div class="digits">
          <span>7854</span>
        </div>
      </div>
      <div class="details">
        <span class="name">Oliver Rodger</span>
        <span class="expires">12/20</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SavedCards",
  data() {
    return {
      selectedCard: null
    };
  },
  methods: {
    selectCard(index) {
      if (this.selectedCard === index) {
        this.selectedCard = null;
      } else {
        this.selectedCard = index;
      }
    },
    isSelected(index) {
      return index === this.selectedCard;
    },
    isNotSelected(index) {
      return this.selectedCard !== null && this.selectedCard !== index;
    }
  }
};
</script>

