<template>
  <div class="player">
    <div class="left">
      <img src="../assets/icons/Track-Note.svg" />
      <span class="name">{{ title }}</span>
    </div>
    <div class="middle">
      <img @click="togglePlay()" src="../assets/icons/Track-Play.svg" />
      <div class="progress">
        <div class="time">
          <span>{{ currentTime | duration }}</span>
          <span>{{ audioDuration | duration }}</span>
        </div>
        <div class="progressbar">
          <div
            class="line"
            :class="{ active: playing }"
            :style="progressbarStyle"
          ></div>
        </div>
      </div>
    </div>
    <div class="right" v-if="!download">
      <img src="../assets/icons/Track-Spotify.svg" />
      <span class="plays">10,267</span>
    </div>
    <div class="right-download" v-if="download">
      <img src="../assets/icons/Track-Download.svg" />
    </div>
  </div>
</template>

<script>
export default {
  name: "AudioPlayer",
  props: {
    download: Boolean
  },
  data() {
    return {
      audio: null,
      playing: false,
      paused: false,
      loadedMetadata: false,
      currentTime: 0,
      title: "Hypnotize",
      url:
        "https://archive.org/download/cd_hypnotize_system-of-a-down_1/disc1/04.%20System%20Of%20A%20Down%20-%20Hypnotize_sample.mp3"
    };
  },
  created: function() {
    this.audio = new Audio(this.url);
    this.audio.onloadedmetadata = () => {
      this.loadedMetadata = true;
    };
  },
  computed: {
    progressbarStyle() {
      let progress = 0;
      if (this.loadedMetadata) {
        progress = (this.currentTime / this.audioDuration) * 100;
      }
      return {
        width: `${progress}%`
      };
    },
    audioDuration() {
      return this.loadedMetadata ? this.audio.duration : 0;
    }
  },
  filters: {
    duration(seconds) {
      const m = Math.floor((seconds % 3600) / 60)
          .toString()
          .padStart(2, "0"),
        s = Math.floor(seconds % 60)
          .toString()
          .padStart(2, "0");

      return m + ":" + s;
    }
  },
  methods: {
    togglePlay() {
      if (this.playing) {
        this.pause();
      } else {
        this.play();
      }
    },
    play() {
      this.playing = true;
      if (this.paused) {
        this.paused = false;
        return this.audio.play();
      }
      this.audio.ontimeupdate = () => {
        this.currentTime = this.audio.currentTime;
      };
      this.audio.onended = () => {
        this.playing = false;
        this.paused = false;
      };
      this.audio.play();
    },
    pause() {
      this.playing = false;
      this.paused = true;
      this.audio.pause();
    }
  }
};
</script>

